import React, {useState, useRef} from 'react';
import {roles} from '../settings';





let selectedRole = 0;
export const SetDinamicTask = function ({item, useNewTaskRole, useNewTask, setShowSetDinamicTask}){

  

  const selectRole = (
    <select 
      className="login-form__role login-form__field" 
      onChange={(e)=>{useNewTaskRole.current=e.target.value}}>
      <option>-</option>
      {roles.map((item) => {
        if (item.index !== 7)
            return <option key={item.index}value={item.index}>{item.value}</option> 
      })}
      
    </select>
)



  const clickHandler = () => {
    useNewTask.item = item;
    console.log('use description > ', useNewTask.current)
    setShowSetDinamicTask(false);
  }

  return (
    <div className='comment-background'>
      <div className="comment-form">
        <h2 className="login-form__title">   </h2>
        <form className="login-form" onSubmit={(e)=>{e.preventDefault()}}>
          {selectRole}
            <input        
              type="text" 
              className="login-form-email__field login-form__field" 
              onChange={(e) =>(useNewTask.current = e.target.value)}
              placeholder="  "/>
               <button className="form-button" onClick={(e) => clickHandler()}>  </button>
          </form>
      </div>
    </div>
  )
}