import React, {useState, useRef} from 'react';
import {roles} from '../settings';


let selectedRole = 0;
export const LoginForm = function ({authUser, setAuthInfo, setRole}){
  let emailRef = useRef('');
  let passwordRef = useRef('');

  if(authUser) {
    console.log('user roles > ', JSON.parse(authUser.tags));
  }

  function login() {
    if(!authUser) {
      setAuthInfo(()=>{
        return {
          'email': emailRef.current,
          'password': passwordRef.current
        }
      });
    } else if (selectRole){
      localStorage.setItem('role', selectedRole);
          setRole(selectedRole);     
    }
  }

  
  const selectRole = (
        <select className="login-form__role login-form__field" onChange={(e)=>{selectedRole=e.target.value}}>
          <option>-</option>
          {authUser && roles.map((item) => {
            const tags = JSON.parse(authUser.tags);
              if(tags.ids.indexOf(item.index) !== -1){
                return <option key={item.index}value={item.index}>{item.value}</option>
              }
            
          })}
          
        </select>
  )

  return (
    <>
      <div className="logo-text">
        {/* <a className="logo-link" href="#">
          
        </a> */}
        <img src='./logo.svg' style={{'maxWidth': '80%'}}/>
      </div>

      {/* <h2 className="login-form__title"></h2> */}
      {/* <img src='./logo.png' style={{'maxWidth': '80%'}}/> */}
      <form className="login-form" onSubmit={(e)=>{e.preventDefault()}}>
        {!authUser &&
        <div className="login-form-input__wrapper">
          <label className="login-form__label">  \ </label>
          <input onChange={(e) =>(emailRef.current = e.target.value)} 
            
            type="email" 
            className="login-form-email__field login-form__field" 
            placeholder="EmailAdress@gmail.com"/>
        </div>
}
        {!authUser &&
        <input 
          onChange={(e) =>(passwordRef.current = e.target.value)} 
          
          type="password" className="login-form__field" placeholder=""/>
        }
        
        {authUser && selectRole}

        <button onClick={()=>{login();}} className="form-button">  </button>
      </form>
    </>
  );
}