import React, {useState, useRef, useCallback, useEffect} from 'react';
import {roles, statuses, axiosQueryRoot} from '../settings';
import axios from 'axios';
import { CookDescription } from './CookDescription';





let selectedRole = 0;
let socket;
export const CookBoard = function ({userRole, authInfo, setUserRole, setAuthInfo}){


  const [todoList, setTodoList] = useState([]);
  const [notionCount, setNotionCount] = useState(0);
  const [showCookDescription, setShowCookDescription] = useState(false);
  let useDescription = useRef('');
  let useItem = useRef(0);



  useEffect(() => {
    if (userRole ) {
      if (userRole ) {
        console.log(authInfo);
        console.log('user role > ', userRole)
        axios.post('http://' + axiosQueryRoot + '/board/take', {
          'email': authInfo.email,
          'tag_id': userRole,
        }).then((resp) => {
          console.log(resp.data);
          const data = resp.data;

          setTodoList(data);
        })
      }
    }
  }, []);


    useEffect(() => {
      socket = new WebSocket('ws://localhost:8000/ws/' + userRole);
  
      socket.onopen = () => {
        console.log('WebSocket connected');
        // socket.send('Hello from client!');
      };
  
      socket.onmessage = (event) => {
        console.log('Message from server:', event.data);
        if (!JSON.parse(event.data).items.length) {
          setUserRole(false);
          setAuthInfo(false);
          localStorage.removeItem('role');
          localStorage.removeItem('username');
          localStorage.removeItem('email');
        }
        setTodoList(JSON.parse(event.data));
      };

      socket.onerror = (event) => {
        console.log(event)
      }
  
      return () => {
        socket.close();
        console.log('close')
      };
    }, []);



  const changeTaskStatus = (board_id, item_id, status, comment) => {
    // setItemListNewData({
    //   'board_id': board_id,
    //   'item_id': item_id,
    //   'status': status,
    //   'comment': comment
    // })
  }
 


  const checkItem = (item_id, morning, evening, empty, work_shift) => {
  
      // socket.send({type: 'task complete', tag_id: item_id});
      console.log('work')
      // socket.onmessage = (event) => {
      //   console.log('Message from server:', event.data);
      // };
      socket.send(JSON.stringify({
        type: 'cook_info', 
        'task_id': item_id,
        'morning': '',
        'evening': '',
        'empty': '',
        'description': useDescription,
        'work_shift': '',
      }));
      console.log(socket.onmessage)

      useDescription.current = '';
  }

  let board = 0;
  let item= 0;
  const closeItem = (board_id, item_id) => {

    board = board_id;
    useItem.current = item_id;
    console.log('itemid > ', item);
    console.log('boardid > ', board);


      useDescription.current = '';
    setShowCookDescription(true);
    
  }


  useEffect(() => {
    console.log(useDescription.current)
    console.log('item', useItem.current)
    if (useDescription.current && !showCookDescription){
      console.log(useItem.current);
      console.log(useDescription.current);
      socket.send(JSON.stringify({
        type: 'cook_info', 
        'task_id': useItem.current.id,
        'morning': useItem.current.morning,
        'evening': useItem.current.evening,
        'empty': useItem.current.standard - ((parseInt(useItem.current.morning) || 0) + (parseInt(useItem.current.evening) || 0)),
        'description': useDescription.current,
        'work_shift': useItem.current.work_shift,
      }));
      // changeTaskStatus(board, item, statuses['error'])
      // setTodoList((prevState) => {
      //   console.log(prevState)
      //   localStorage.setItem('todoList', JSON.stringify({
      //     'id': prevState.id,
      //     'items': prevState.items.filter((item_e) => item_e.id !== item)
      //   }));

      //   return {
      //     'id': prevState.id,
      //     'items': prevState.items.filter((item_e) => item_e.id !== item)
      //   }
      // });
      // statusChanged.current = true;
      // console.log(useComment.current)

      useDescription.current = '';
      // board = 0;
      // item = 0;
    }
    
  }, [showCookDescription]);


  function setMorning(item_id, value) {
    setTodoList((state) => {
      return {
        ...state, 
       ...state.items.map((item) => {
        if(item.id === item_id) {
          item.morning = parseInt(value);
        }
        return item;
       })
      }
    })
  }

  function setEvening(item_id, value) {
    setTodoList((state) => {
      return {
        ...state, 
       ...state.items.map((item) => {
        if(item.id === item_id) {
          item.evening = parseInt(value);
        }
        return item;
       })
      }
    })
  }




  return (
    <div className="todo-list cook-list">
      <h2 className="todo-list__title">
        {notionCount && <span className="todo-list__notification-marker">{notionCount}</span>}  </h2>
      <ul className="todo-list__list">
        {todoList && todoList.items && todoList.items.map(function(item) {
          return (
            <li key={item.id} className={"todo-list__item " + (item.is_notion ? ' todo-list__notion' : '')}>

              <h4 className="login-form__title product-title">{item.name}</h4>
              <div className="login-form__title product-title2">{item.arab}</div>
              <div className="product-count">
                <span className="product-count__number mr-5">{item.standard - ((parseInt(item.morning) || 0) + (parseInt(item.evening) || 0))}</span>
                <span className="product-count__text">{item.standard} </span>  
              </div>
              <form className="login-form" onSubmit={(e)=>{e.preventDefault()}}>
                <div className="input-group row justify-content-between">
    
                  <input 
                    onChange={(e) =>{setMorning(item.id, e.target.value)}} 
                    type="text" 
                    className="login-form-email__field login-form__field col-5" 
                    placeholder=""
                    disabled={item.work_shift === "" ? false : true}
                    value={isNaN(parseInt(item.morning)) ? 0 : item.morning}/>

                <input 
                  onChange={(e) =>{setEvening(item.id, e.target.value)}}                  
                  type="text" 
                  className="login-form__field col-5" 
                  placeholder=""
                  disabled={item.work_shift === "" ? false : true}
                  value={isNaN(parseInt(item.evening)) ? 0 : item.evening}/>
                </div>

                <button onClick={()=>{useItem.current = item; setShowCookDescription(true)}} className="form-button">  \ </button>
              </form>
              
            </li>
          );
        })}
        

      </ul>
      
      {showCookDescription && <CookDescription
                              useDescription={useDescription} 
                              showCookDescription={setShowCookDescription}
                              changeTaskStatus={changeTaskStatus}
                              item={useItem}
                              />}
    </div>

  )
}



