import React, {useState, useRef} from 'react';
import {roles} from '../settings';


let selectedRole = 0;
export const CommentForm = function ({item, useComment, showCommentForm}){
  
  const clickHandler = () => {
    useComment.item = item;
    console.log('use comment > ', useComment.current)
    showCommentForm(false);
  }

  return (
    <div className='comment-background'>
      <div className="comment-form">
        <h2 className="login-form__title">   </h2>
        <form className="login-form" onSubmit={(e)=>{e.preventDefault()}}>
            <input        
              type="text" 
              className="login-form-email__field login-form__field" 
              onChange={(e) =>(useComment.current = e.target.value)}
              placeholder="  "/>
               <button className="form-button" onClick={(e) => clickHandler()}>  </button>
          </form>
      </div>
    </div>
  )
}