import React, {useState, useRef, useCallback, useEffect} from 'react';
import {roles, statuses, axiosQueryRoot} from '../settings';
import axios from 'axios';
import { CommentForm } from './CommentForm';
import io from 'socket.io-client'
import { SetDinamicTask } from './SetDinamicTask';



let selectedRole = 0;
let socket;

export const WaiterBoard = function ({ws, userRole, authInfo, setUserRole, setAuthInfo}){
  const [todoList, setTodoList] = useState([]);
  const [notionCount, setNotionCount] = useState(0);
  const [showCommentForm, setShowCommentForm] = useState(false);
  const [showSetDinamicTask, setShowSetDinamicTask] = useState(false);
  let useComment = useRef('');
  let useItem = useRef(0);

  let useNewTask = useRef('');
  let useNewTaskRole = useRef(0);





   useEffect(() => {
    if (userRole ) {
      if (userRole ) {
        console.log(authInfo);
        axios.post('http://' + axiosQueryRoot + '/board/take', {
          'email': authInfo.email,
          'tag_id': userRole,
        }).then((resp) => {
          console.log(resp.data);
          const data = resp.data;

          setTodoList(data);
        })
      }
    }
  }, []);


    useEffect(() => {
      socket = new WebSocket('ws://localhost:8000/ws/' + userRole);
  
      socket.onopen = () => {
        console.log('WebSocket connected');
        // socket.send('Hello from client!');
      };
  
      socket.onmessage = (event) => {
        console.log('Message from server:', event.data);
        if (!JSON.parse(event.data).items.length) {
          setUserRole(false);
          setAuthInfo(false);
          localStorage.removeItem('role');
          localStorage.removeItem('username');
          localStorage.removeItem('email');
        }
        setTodoList(JSON.parse(event.data));
      };

      socket.onerror = (event) => {
        console.log(event)
      }
  
      return () => {
        socket.close();
        console.log('close')
      };
    }, []);



  const changeTaskStatus = (board_id, item_id, status, comment) => {
    // setItemListNewData({
    //   'board_id': board_id,
    //   'item_id': item_id,
    //   'status': status,
    //   'comment': comment
    // })
  }
 


  const checkItem = (board_id, item_id) => {
  
      // socket.send({type: 'task complete', tag_id: item_id});
      console.log('work')
      // socket.onmessage = (event) => {
      //   console.log('Message from server:', event.data);
      // };
      socket.send(JSON.stringify({
        type: 'done', 
        'task_id': item_id,
        'status': statuses['success']
      }));
      console.log(socket.onmessage)

      useComment.current = '';
  }

  let board = 0;
  let item= 0;
  const closeItem = (board_id, item_id) => {

    board = board_id;
    useItem.current = item_id;
    console.log('itemid > ', item);
    console.log('boardid > ', board);


    changeTaskStatus(board_id, item_id, statuses['error'])

      useComment.current = '';
    setShowCommentForm(true);
    
  }


  useEffect(() => {
    console.log(useComment.current)
    console.log('item', useItem.current)
    if (useComment.current && !showCommentForm){
      console.log(useComment.current)
      console.log('item', useItem.current)
      console.log('send comment')
      socket.send(JSON.stringify({
        type: 'waiter task error', 
        'task_id': useItem.current, 
        'comment': useComment.current,
        'status': statuses['error'] 
      }));

      useComment.current = '';

    }
    
  }, [showCommentForm]);


  useEffect(() => {
    if (useNewTask.current && useNewTaskRole.current && !showSetDinamicTask){
      console.log(useComment.current)
      console.log('item', useItem.current)
      console.log('send comment')
      socket.send(JSON.stringify({
        type: 'create_dynamic_task', 
        'task_text': useNewTask.current,
        'role_id': useNewTaskRole.current,
        'author_role_id': localStorage.getItem('role'),
        'author_name': localStorage.getItem('username')
      }));

      useComment.current = '';

    }
    
  }, [showSetDinamicTask]);



  function renderListItem(item) {
    return (
      <li key={item.id} className={"todo-list__item " + (item.is_dyn_task ? ' todo-list__notion' : '')}
      style={{'border': item.is_alert ? '1px solid red' : ''}}
      >
        {item.is_dyn_task && <img className="todo-list__notion-marker" src="./dyn_task.svg" alt="notion marker"/>}
        <span className="todo-list__time">{item.time}</span>

        <p className="todo-list__text">
        {item.name}
        </p>
        {item.is_dyn_task &&
          <p className="todo-list__comment">
            {item.author} <br/>
            {
              roles.map((role) => { 
                if( role.index === parseInt(item.author_role_id)) {
                  return role.value;
                }
              })
              // item.author_role_id
            }    
          </p>
        
        }
        <div className="todo-list__buttons">
          <button className="todo-list__button todo-list__close" onClick={()=>{closeItem(todoList.id.id, item.id)}}>
            <img src="./x.svg" alt="close"/>
          </button>
          <button className="todo-list__button todo-list__check" onClick={()=>{checkItem(todoList.id.id, item.id)}}>
            <img src="./check.svg" alt="check"/>
          </button>
        </div>
        
      </li>
    );
  }


  return (
    <div className="todo-list">
      <h2 className="todo-list__title" onClick={(e)=>{setShowSetDinamicTask(true)}}>
        {todoList && todoList.items  && <span className="todo-list__notification-marker">
          {
          todoList.items.filter((item) => { 
            if (item.is_dyn_task) 
              return item;}).length 
          }</span>}  </h2>
      <ul className="todo-list__list">
        {todoList && todoList.items && todoList.items.map(renderListItem)}
        {todoList && !todoList.items && todoList.map(renderListItem)}
        

      </ul>
      
      {showCommentForm && <CommentForm item={item} useComment={useComment} showCommentForm={setShowCommentForm}/>}

      {(userRole == 6 || userRole == 7) && showSetDinamicTask && 
        <SetDinamicTask 
          item={item} 
          useNewTaskRole={useNewTaskRole} 
          useNewTask={useNewTask} 
          setShowSetDinamicTask={setShowSetDinamicTask}/>
        }
    </div>

  )
}